// BidTable.tsx
export const BidTable = ({ bids }: { bids: [string, string][] }) => {
    let total = 0;
    const data = bids.map(([price, qty]) => {
      total += Number(qty);
      return [price, qty, total];
    });
    const maxTotal = data[data.length - 1]?.[2] || 1;
  
    return (
      <div>
        {data.map(([price, qty, total]) => (
          <OrderRow
            key={price}
            price={price}
            quantity={qty}
            total={total}
            maxTotal={maxTotal}
            color="rgba(1, 167, 129, 0.325)"
          />
        ))}
      </div>
    );
  };
  