// utils/SignalingManager.ts
export class SignalingManager {
    private static instance: SignalingManager;
    private ws: WebSocket;
    private isOpen = false;
    private queue: any[] = [];
    private callbacks: Record<string, ((data: any) => void)[]> = {};
  
    private constructor() {
      this.ws = new WebSocket("wss://ws.backpack.exchange/");
      this.ws.onopen = () => {
        this.isOpen = true;
        this.queue.forEach((msg) => this.ws.send(JSON.stringify(msg)));
        this.queue = [];
      };
  
      this.ws.onmessage = (event) => {
        const { data } = JSON.parse(event.data);
        const type = data?.e;
        if (type && this.callbacks[type]) {
          this.callbacks[type].forEach((cb) => cb(data));
        }
      };
    }
  
    public static getInstance() {
      if (!this.instance) this.instance = new SignalingManager();
      return this.instance;
    }
  
    public sendMessage(message: any) {
      const msg = { ...message, id: Date.now() };
      if (!this.isOpen) {
        this.queue.push(msg);
      } else {
        this.ws.send(JSON.stringify(msg));
      }
    }
  
    public registerCallback(type: string, callback: (data: any) => void) {
      if (!this.callbacks[type]) this.callbacks[type] = [];
      this.callbacks[type].push(callback);
    }
  
    public deRegisterCallback(type: string, callback: (data: any) => void) {
      this.callbacks[type] = this.callbacks[type]?.filter((cb) => cb !== callback);
    }
  }
  