"use client";

import { useEffect, useRef, useState } from "react";
import { AskTable } from "./AskTable";
import { BidTable } from "./BidTable";
import { SignalingManager } from "@/app/utils/SignalingManager";

export function Depth({ market }: { market: string }) {
  const [bids, setBids] = useState<[string, string][]>([]);
  const [asks, setAsks] = useState<[string, string][]>([]);
  const pendingRef = useRef<{ bids: [string, string][], asks: [string, string][] } | null>(null);
  const throttleRef = useRef<NodeJS.Timeout | null>(null);

  useEffect(() => {
    const handleDepthUpdate = (data: any) => {
      pendingRef.current = {
        bids: data.b,
        asks: data.a
      };

      if (!throttleRef.current) {
        throttleRef.current = setTimeout(() => {
          const update = pendingRef.current;
          if (update) {
            setBids((prev) => mergeOrders(prev, update.bids, true));
            setAsks((prev) => mergeOrders(prev, update.asks, false));
          }
          pendingRef.current = null;
          throttleRef.current = null;
        }, 1000); // Throttle update every 1s
      }
    };

    const signaling = SignalingManager.getInstance();
    signaling.registerCallback("depth", handleDepthUpdate);
    signaling.sendMessage({ method: "SUBSCRIBE", params: [`depth@${market}`] });

    return () => {
      signaling.sendMessage({ method: "UNSUBSCRIBE", params: [`depth@${market}`] });
      signaling.deRegisterCallback("depth", handleDepthUpdate);
      if (throttleRef.current) clearTimeout(throttleRef.current);
    };
  }, [market]);

  return (
    <div>
      <TableHeader />
      <AskTable asks={asks} />
      <div className="text-center text-lg font-bold my-2 text-white">
        Market: {market}
      </div>
      <BidTable bids={bids} />
    </div>
  );
}

const TableHeader = () => (
  <div className="flex justify-between text-xs text-gray-300">
    <div>Price</div>
    <div>Size</div>
    <div>Total</div>
  </div>
);

// Helper to merge incoming depth with existing
function mergeOrders(
  existing: [string, string][],
  updates: [string, string][],
  isBid: boolean
): [string, string][] {
  const map = new Map(existing.map(([price, qty]) => [price, qty]));

  for (const [price, qty] of updates) {
    if (Number(qty) === 0) {
      map.delete(price);
    } else {
      map.set(price, qty);
    }
  }

  const sorted = Array.from(map.entries()).sort((a, b) =>
    isBid ? Number(b[0]) - Number(a[0]) : Number(a[0]) - Number(b[0])
  );

  return sorted.slice(0, 15); // only top 15
}
